.obsidian-admin-row {
	display: flex;
	align-items: flex-end;
	gap: 5px;

	+ .obsidian-admin-row {
		margin-top: 5px;
	}
}

.obsidian-admin-column {
	flex: 1 1 auto;
}

.obsidian-admin-column--icon {
	flex: 0 0 50px;

	> * {
		margin-bottom: 8px;
	}
}

.obsidian-admin-column--drag-handle {
	flex: 0 0 24px;

	> * {
		margin-bottom: 8px;
		margin-right: 0 !important;
		min-width: 24px !important;
		padding: 0 !important;
	}
}

.option--ghost {
	background: #f0f0f0;
}
